<template>
  <div>
    <div class='searchTable' ref='top'>
      <el-card class='card' shadow="hover">
        <el-collapse>
          <el-collapse-item>
            <template slot='title'>
              <span>{{$t("General.SearchAndFilter")}}</span>
              <i class='el-icon-search'></i>
            </template>
            <div class='search'>
              <el-input class='inputBox' v-model='filter.keywords'
                :placeholder='this.$t("News.EnterKeywords")'></el-input>
            </div>

            <div class='filter'>
              <el-select class='filterBox filterBoxLeft' v-model="filter.school" :placeholder='$t("News.FilterSchool")'>
                <el-option :label='$t("News.FilterAll")' value='all'></el-option>
                <el-option :label='$t("User.UserSchool0")' value='0'></el-option>
                <el-option :label='$t("User.UserSchool1")' value='1'></el-option>
                <el-option :label='$t("User.UserSchool2")' value='2'></el-option>
                <el-option :label='$t("User.UserSchool3")' value='3'></el-option>
              </el-select>

              <el-select class='filterBox' v-model="filter.tag" :placeholder='$t("News.FilterTag")' disabled>
              </el-select>
            </div>

            <div class='submit'>
              <el-button @click='clearFilter'>{{$t("News.ClearCondition")}}</el-button>
              <el-button type='primary' @click='getNewsList'>{{$t("News.SubmitCondition")}}</el-button>
            </div>
          </el-collapse-item>
        </el-collapse>
      </el-card>
    </div>

    <div class='newsList' v-if='!listEmpty'>
      <div class='list' v-for='item in list' v-bind:key='item.newsID'>
        <div class='item'>
          <news-item :newsID='item.newsid' :title='item.title' :content='item.content' :userAuth='item.userauth'
            :time='item.time' :like='item.likes' :comments='item.comments' :school='item.school'></news-item>
        </div>
      </div>
      <div class='loadMore'>
        <el-card class='card'>
          <span class='loadMoreClick' v-if='!loadingFull' @click='loadMore'>{{this.$t('General.LoadMore')}}</span>
          <span v-if='loadingMore'>{{this.$t('General.LoadingMore')}}</span>
          <span v-if='loadingFull'>{{this.$t('General.LoadNoMore')}}</span>
        </el-card>
      </div>
    </div>

    <div class='empty' v-if='listEmpty'>
      <el-image :src="require('@/../public/assets/empty.png')" style='width:100px'></el-image>
      <div>{{$t('Hollow.ListEmpty')}}</div>
    </div>

    <div class='floatButton'>
      <el-button circle type='primary' icon='el-icon-upload2' class='floatButton floatButtonTop'
        @click="gotoTop"></el-button>
      <el-button circle type='success' icon='el-icon-refresh' class='floatButton floatButtonRefresh'
        @click="refreshNews"></el-button>
      <el-button circle type='warning' icon='el-icon-plus' class='floatButton floatButtonNew' @click="newNews"
        v-if='allowNew'></el-button>
    </div>

  </div>
</template>

<script>
import axios from 'axios'
import Cookies from 'js-cookie'
import * as server from '../../../server.js'

export default {
  name: 'news-list',
  components: {
    NewsItem: () => import('./NewsItem.vue'),
  },
  data () {
    return {
      list: [],
      filter: {
        keywords: '',
        school: 'all',
        tag: '',
      },
      listEmpty: false,
      allowNew: false,
      page: 0,
      loadingMore: false,
      loadingFull: false,
      listLength: -1
    }
  },
  methods: {
    gotoTop () {
      this.$refs.top.scrollIntoView({ behavior: "smooth", block: "end" });
    },
    refreshNews () {
      this.gotoTop();
      setTimeout(() => {
        this.list = [];
        this.page = 0;
        this.loadingFull = false;
        this.listLength = -1;
        this.getNewsList();
      }, 500)
    },
    newNews () {
      this.$bus.$emit('showNewsNew');
    },
    getNewsList () {
      this.loadingMore = true;
      const loadingInstance = this.loading();
      axios({
        method: 'post',
        url: server.default.getNewsList,
        data: new URLSearchParams({
          'school': this.filter.school,
          'keywords': this.filter.keywords,
          'page': this.page,
        })
      }).then((response) => {
        loadingInstance.close();
        if (response.status != 200) {
          this.alert(this.$t('System.NetworkIssue'), this.$t('System.Error'))
        }
        else {
          if (response.data.status == 'NEWS_GETLIST_COMPLETE') {
            this.list.push(...response.data.list)
            this.listEmpty = false;
            this.loadingMore = false;
            if (this.listLength < 0) {
              this.listLength = response.data.listLength;
            }
            if (response.data.listLength <= this.list.length) {
              this.loadingFull = true;
            }
          }
          else if (response.data == 'NEWS_GETLIST_EMPTY') {
            if (this.list.length == 0) {
              this.listEmpty = true;
            }
          }
        }
      })
    },
    clearFilter () {
      this.filter.keywords = '';
      this.filter.school = 'all';
      this.refreshNews();
    },
    getUser () {
      if (!Cookies.get('loginCookie')) {
        this.goLogin();
        return;
      }

      const loadingInstance = this.loading();

      axios({
        method: 'post',
        url: server.default.user,
        data: new URLSearchParams({
          'type': 'me',
          'cookie': Cookies.get('loginAs')
        })
      }).then((response) => {
        loadingInstance.close();
        if (response.status != 200) {
          this.alert(this.$t('System.NetworkIssue'), this.$t('System.Error'))
        }
        else {
          if (response.data == 'USER_NOMATCH') {
            this.alert(this.$t('System.IllegalRequest'), this.$t('System.Error'));
          }
          else if (response.data.status == 'USER_FOUND') {
            if (response.data.userstatus >= 2) {
              this.allowNew = true;
            }
            else {
              this.allowNew = false;
            }
          }
          else {
            this.alert('Unexpected Response!', this.$t('System.Error'));
          }
        }
      });
    },
    loadMore () {
      if (!this.loadingMore) {
        this.loadingMore = true;
        this.page++;
        this.getNewsList();
      }
    }
  },
  mounted () {
    this.$bus.$on('refreshNewsList', this.refreshNews);

    this.getNewsList();
    this.getUser();
  },
  beforeDestroy () {
    this.$bus.$off('refreshNewsList');
  }
}
</script>

<style scoped>
.floatButton {
  position: fixed;
  right: 40px;
  font-size: 25px;
  cursor: pointer;
}
.floatButtonTop {
  bottom: 200px;
}
.floatButtonRefresh {
  bottom: 140px;
}
.floatButtonNew {
  bottom: 80px;
}
.searchTable {
  width: 80%;
  margin-left: 10%;
  margin-bottom: 20px;
}
.card {
  border-radius: 15px;
}
::v-deep .el-collapse-item__header {
  font-size: 20px;
}
::v-deep .el-collapse-item__header span {
  margin-right: 2%;
}
.inputBox {
  width: 80%;
  text-align: left;
  margin: 20px;
}
.filterBox {
  width: 35%;
  margin-bottom: 20px;
}
.filterBoxLeft {
  margin-right: 20px;
}
.empty {
  margin-top: 10%;
  font-size: 30px;
}
.item {
  margin-bottom: 30px;
}
.loadMore {
  width: 40%;
  margin-left: 30%;
  margin-bottom: 50px;
}
.loadMore .card {
  border-radius: 15px;
}
.loadMore .loadMoreClick {
  cursor: pointer;
}
</style>